.buttonContainer {
  position: absolute;
  z-index: 10;
}

.buttonContainerCentered {
  display: flex;
  justify-content: center;
  width: 100%;
  margin-top: 0.5rem;
}

.buttonImage {
  width: 110px;
  height: 55px;
  cursor: pointer;
  transition: all 0.3s ease;
  filter: drop-shadow(0 4px 8px rgba(0, 0, 0, 0.5));
  display: block;
}

.buttonImage:hover {
  transform: translateY(-2px);
  filter: drop-shadow(0 6px 12px rgba(0, 0, 0, 0.7));
}
